import React, { useEffect, useState } from 'react';

const sliderImages = [
  process.env.PUBLIC_URL + '/images/1000278926.jpg',
  process.env.PUBLIC_URL + '/images/1000278927.jpg',
  process.env.PUBLIC_URL + '/images/1000278928.jpg',
  process.env.PUBLIC_URL + '/images/1000278929.jpg',
];

const ProductList = ({ onAddToCart }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('products.json')
      .then(res => res.json())
      .then(data => {
        setProducts(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <p>Loading products...</p>;

  return (
    <div
      style={{
        height: '100vh',
        overflowY: 'scroll',
        scrollSnapType: 'y mandatory',
        WebkitOverflowScrolling: 'touch'
      }}
    >
      {products.map((product, idx) => (
        <section
          key={product.id}
          style={{
            height: '100vh',
            width: '100vw',
            scrollSnapAlign: 'start',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            position: 'relative',
            background: '#fff'
          }}
        >
          <img
            src={sliderImages[idx]}
            alt={product.title}
            style={{
              width: '100vw',
              height: '100vh',
              objectFit: 'cover',
              position: 'absolute',
              top: 0,
              left: 0,
              zIndex: 1
            }}
          />
          <div
            style={{
              position: 'relative',
              zIndex: 2,
              background: 'rgba(0,0,0,0.4)',
              color: '#fff',
              padding: '24px 16px',
              borderRadius: 16,
              marginBottom: 24,
              textAlign: 'center'
            }}
          >
            <h2 style={{ fontSize: 28, fontWeight: 700 }}>{product.title}</h2>
            <p style={{ fontSize: 22, fontWeight: 600 }}>â‚¹{product.price}</p>
            <button
              onClick={() => onAddToCart(product)}
              style={{
                background: 'linear-gradient(90deg, #007bff 60%, #00c6ff 100%)',
                color: '#fff',
                border: 'none',
                borderRadius: 24,
                padding: '14px 40px',
                fontSize: 20,
                fontWeight: 700,
                boxShadow: '0 2px 8px rgba(0,123,255,0.08)',
                cursor: 'pointer',
                marginTop: 16
              }}
            >
              Add to Cart
            </button>
          </div>
        </section>
      ))}
      <style>{`
        html, body, #root {
          height: 100%;
          margin: 0;
          padding: 0;
        }
        @media (max-width: 600px) {
          section {
            border-radius: 0 !important;
          }
        }
      `}</style>
    </div>
  );
};

export default ProductList;