import React from 'react';

function ShoppingCart({ cart, ordered, onQuantityChange, onRemove, onOrderNow, onCancel }) {
  const total = cart.reduce((sum, item) => sum + item.price * item.quantity, 0);

  // WhatsApp order message
  const getWhatsAppMessage = () => {
    if (cart.length === 0) return '';
    let message = 'Order Details:%0A';
    cart.forEach(item => {
      message += `${item.name} x ${item.quantity} = $${item.price * item.quantity}%0A`;
    });
    message += `%0ATotal: $${total}`;
    return message;
  };

  const handleOrderNowClick = () => {
    if (cart.length === 0) return;
    let message = 'ðŸ›’ *Order Details*:%0A';
    cart.forEach(item => {
      message += `â€¢ ${item.name} x ${item.quantity} = â‚¹${item.price * item.quantity}%0A`;
    });
    message += `%0A*Total:* â‚¹${total}`;
    const whatsappUrl = `https://wa.me/916381163597?text=${message}`;
    window.open(whatsappUrl, '_blank', 'noopener,noreferrer');
    onOrderNow();
  };

  return (
    <div className="shopping-cart-container">
      <h2>Shopping Cart</h2>
      {cart.length === 0 ? (
        ordered ? <p>Order placed successfully!</p> : <p>Your cart is empty.</p>
      ) : (
        <>
          <ul>
            {cart.map(item => (
              <li key={item.id}>
                <span style={{ fontWeight: 600 }}>{item.name}</span>
                <span>
                  <button onClick={() => onQuantityChange(item.id, item.quantity - 1)} disabled={item.quantity <= 1} style={{ marginRight: 4 }}>-</button>
                  <input type="number" min="1" value={item.quantity} onChange={e => onQuantityChange(item.id, Number(e.target.value))} />
                  <button onClick={() => onQuantityChange(item.id, item.quantity + 1)} style={{ marginLeft: 4 }}>+</button>
                </span>
                <span style={{ color: '#007bff', fontWeight: 600 }}>
                  â‚¹{item.price} x {item.quantity} = â‚¹{item.price * item.quantity}
                </span>
                <button onClick={() => onRemove(item.id)} style={{ marginLeft: 8, background: '#eee', color: 'red', border: 'none', borderRadius: 8, cursor: 'pointer', fontWeight: 700 }}>Remove</button>
              </li>
            ))}
          </ul>
          <p><strong>Total:</strong> â‚¹{total}</p>
          <button onClick={handleOrderNowClick} style={{ marginRight: 10, background: 'linear-gradient(90deg, #007bff 60%, #00c6ff 100%)' }}>Order Now (WhatsApp)</button>
          <button onClick={onCancel} style={{ background: 'linear-gradient(90deg, #ff416c 60%, #ff4b2b 100%)' }}>Cancel</button>
        </>
      )}
    </div>
  );
}

export default ShoppingCart;
